#DC{
    display: flex;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 1001;
    &>div{
        flex: 1 1 0;
        position: relative;
        &>button{
            width: 100%;
            height:100%;
            background: rgba($color: #000000, $alpha: 0.3);
            &:hover{
                background: none;
            }
        }
        &>div{
            width: 200px;
            height: 100px;
            line-height: 100px;
            position: absolute;
            transform: translateX(-50%) rotate(-25deg);
            left: 50%;
            bottom: 100px;
            background: #253;
            text-align: center;
        }
    }
    .dog{
        background: #fff;
    }
    .cat{
        background: #555;
    }
    .m_logo{
        width: 150px;
        height: 150px;
        position: absolute;
        transform: translateX(-50%);
        top: 10px;
        left: 50%;
        background: #222;
        z-index: 1;
    }
}
#main_header{
    width: 100%;
    height: 100px;
    position: fixed;
    background: #fff;
    z-index: 1000;
    h1{
        height: 50px;
        text-align: center;
    }
    .cart{
        width: 50px;
        height: 50px;
        background: #333;
        position: absolute;
        right: 50px;
        top: 0px;
    }
    .gnb{
        border-top: 1px #ccc solid;
        border-bottom: 1px #ccc solid;
        padding: 0px 50px;
        height: 50px;
        line-height: 50px;
        display: flex;
        justify-content: center;
        background: #fff;
        li{
            padding: 0px 30px;
        }
        &>li:first-of-type,li:nth-of-type(5){
            display: block;
            background: #ccc;
            cursor:default;
        }
        &>li:nth-of-type(5){ background: #555;}
        a{
            display: block;
            color: #333;
        }
        li:nth-of-type(2){
            &:hover .dog1{display: flex;}
        }
        li:nth-of-type(3){
            &:hover .dog2{display: flex;}
        }
        li:nth-of-type(4){
            &:hover .dog3{display: flex;}
        }
        li:nth-of-type(6){
            &:hover .cat1{display: flex;}
        }
        li:nth-of-type(7){
            &:hover .cat2{display: flex;}
        }
        li:nth-of-type(8){
            &:hover .cat3{display: flex;}
        }
        .sub{
            width: 100%;
            height: 300px;
            background: #777;
            position: absolute;
            top: 101px;
            left: 0px;
            padding: 0px 100px;
            box-sizing: border-box;
            border-top: 1px #ccc solid;
            border-bottom: 1px #ccc solid;
            display: none;
            .nav_txt{
                width: 20%;
                background: #555;
            }
            ul{
                width: 50%;
                background: #fff;
                padding-left: 50px;
            }
            .nav_img{
                width: 30%;
                background: #222;
            }
        }
    }
}
#main_banner{
    padding-top: 150px;
    height: 500px;
    .slick-slider{
        width: 100%;
        height: 100%;
        background: #ccc;
        .slick-list{
            width: 100%;
            height: 100%;
            .slick-track{
                width: 100%;
                height: 100%;
            }
        }
        .slick-dots{
            bottom: 20px;
            li button:before{
                font-size: 16px;
                color: #333;
            }
            li.slick-active button:before{
                opacity: 1;
                color: #fff;
            }
        }
    }
}
#main_footer{
    width:100%;
    height: 500px;
    margin-top: 50px;
    border-top: 1px #ccc solid;
    .footer_top{
        height: 150px;
        padding: 50px 100px;
        border-bottom: 1px #ccc solid;
        display: flex;
        &>div{
            flex: 1 1 0;
            border-left: 1px #ccc solid;
            padding-left: 50px;
        }
        &>div:first-of-type{ border-left: 0px; }
        .parcel{
            button{
                width: 200px;
                height: 30px;
                line-height: 28px;
                border: 1px #ccc solid;
                border-radius: 5px;
                background: #fff;
                a{  
                    height: 100%;
                    display: block;
                    color: #555;
                }
            }
        }
        h2{ margin-bottom: 10px; }
        p{ margin-bottom: 10px; }
    }
    .footer_bottom{
        height: 100px;
        padding: 50px 100px;
        li{
            display: flex;
            margin-bottom: 10px;
        }
        span{
            padding:0px 20px;
            color: #777;
        }
        p:first-of-type>span{
            padding-left: 0px;
        }
        p:nth-of-type(2)>span::before{
            content: "|";
            padding-right: 20px;
            color: #000;
        }
    }
    .copyright{
        width: 100%;
        height: 50px;
        line-height: 50px;
        background: #000;
        color: #fff;
        text-align: center;
    }
}
#main{
    margin-top: 50px;
    h2{ 
        text-align: center;
        margin-bottom: 50px;
    }
    .tab_box>ul{
        display: flex;
        justify-content: center;
        li{ 
            text-align: center;
            width: 150px;
            height: 50px;
            line-height: 50px;
            border: 1px #ccc solid;
            button.active{
                background: #000;
                color: #fff;
            }
            button{
                width: 100%;
                height: 100%;
                cursor: pointer;
            }
        }
        li:nth-of-type(2){
            border-left: 0px;
            border-right: 0px;
        }
    }
    .best_box{
        margin-top: 50px;
        background: #ccc;
        &>div>ul{
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 50px;
            row-gap: 50px;
            a{
                display: block;
                width: 100%;
                height: 100%;
            }
            img{
                width: 100%;
                height: 300px;
                background: #000;
            }
            h3{
                color: #222;
                padding: 10px 0px;
                border-bottom: 1px #fff solid;
            }
            p{  
                margin-top: 10px;
                color: #555;
            }
        }
        button{
            width: 200px;
            height: 50px;
            background: #333;
            margin: 0 auto;
            margin-top: 50px;
            color: #fff;
            cursor: pointer;
        }
    }
}
#sub_banner{
    margin-top: 100px;
    height: 300px;
    .sub_banner{
        width: 100%;
        height: 100%;
        background: #ccc;
    }
}
#review{
    .slick-track{
        height: 500px;
        &>div{
            height: 100%;
        }
    }
    .slick-slider{
        margin: 0 auto;
        margin-top: 100px;
        width: 90%;
        background: #ccc;
        .slick-prev:before, .slick-next:before{
            font-size: 50px;
            opacity: 1;
            color: #000;
        }
        .slick-next{
            right: -30px;
        }
        .slick-prev{
            z-index: 100;
            left: -60px;
        }
        .slick-dots li.slick-active button:before{
            opacity: 1;
        }
        .review{
            display: flex;
            height: 500px;
            background: #fff;
            justify-content: space-around;
            li{
                width: 30%;
                background: #ccc;
                border-radius: 15px;
            }
        }
    }
}
#sub_page{
    padding-top: 150px;
    &>h2{
        text-align: center;
        padding-bottom: 50px;
        border-bottom: 1px #ccc solid;
    }
    .FoodList{
        &>ul{
            margin-top: 50px;
            padding-bottom: 50px;
            display: flex;
            justify-content: center;
            border-bottom: 1px #ccc solid;
            li{
                height: 50px;
                button{
                    padding: 0px 30px;
                    border-bottom: 1px #ccc solid;
                }
            }
        }
    }
    .FoodSolt{
        &>ul{
            margin-top: 50px;
            display: flex;
            justify-content: flex-end;
            border-bottom: 1px #ccc solid;
            li{
                height: 30px;
                button{
                    padding: 0px 15px;
                }
            }
        }
    }
    button{
        width: 100%;
        height: 100%;
        border-top: 1px #ccc solid;
        border-left: 1px #ccc solid;
        cursor: pointer;
        &:last-child{border-right: 1px #ccc solid;}
        &.list{
            background: #333;
        }
        &.Solt{
            background: #555;
        }
    }
    .Food{
        margin-top: 50px;
        ul{
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 50px;
            row-gap: 50px;
            padding-bottom: 50px;
            border-bottom: 1px #ccc solid;
            li{
                width: 100%;
                height: 400px;
            }
            figure{
                width: 100%;
                height: 100%;
            }
            img{
                width: 100%;
                height: 80%;
                border: 1px #ccc solid;
                box-sizing: border-box;
            }
            figcaption{
                width: 100%;
                height: 20%;
                h3,p{
                    height: 50%;
                    line-height: 40px;
                }
            }
        }
    }
    .listNumber{
        margin-top: 25px;
        ol{
            &::before{
                content: "<";
                line-height: 48px;
                padding-right: 15px;
            }
            &::after{
                content: ">";
                line-height: 48px;
                padding-left: 15px;
            }
            display: flex;
            justify-content: center;
            li{
                margin: 0px 5px;
            }
            button{ 
                background: #ccc;
                padding: 15px;
            }
        }
    }
}
